import React, { Component } from 'react'
import counterpart from 'counterpart';
import Translate from 'react-translate-component'; // displays registered translations
import pl from '../lang/pl.js'
import en from '../lang/en.js'

counterpart.setLocale('{this.props.language}');

export default class Introduction extends Component {
  
  render() {
    return (
      <div>
        
        
        <section id="colorlib-hero" className="js-fullheight" data-section="home">
          <div className="flexslider js-fullheight">
            <ul className="slides">
              <li style={{backgroundImage: 'url(images/img_bg.jpg)'}}>
                <div className="overlay" />
                <div className="container-fluid">
                  
                  <div className="row">
                    <div className="col-md-6 col-md-offset-3 col-md-pull-3 col-sm-12 col-xs-12 js-fullheight slider-text">
                      <div className="slider-text-inner js-fullheight">
                        <div className="desc">
                        <Translate content="copy.h6" component="h6" unsafe={true}></Translate>
                        <Translate content="copy.h5" component="h5" unsafe={true}></Translate>
                          <p><a className="btn btn-primary btn-learn" href="https://tzils.github.io/portcv/images/cv-iwaniuk-resume.pdf" target="_blank" rel="noopener noreferrer">Wyświetl CV w pdf<i className="icon-download4" /></a></p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
             
            </ul>
          </div>
        </section>
      </div>
    )
  }
}