import React, { Component } from 'react'
import counterpart from 'counterpart';
import Translate from 'react-translate-component'; // displays registered translations
import pl from '../lang/pl.js'
import en from '../lang/en.js'

counterpart.setLocale('{this.props.language}');

export default class About extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-about" data-section="about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-12">
                <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
                <div className="col-md-12">
                    <div className="about-desc">
                    <Translate content="about.h2" component="h2" className="colorlib-heading" unsafe={true}></Translate>
                    <Translate content="about.p" component="p" unsafe={true}></Translate>
                    
                   
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
        
      </div>
    )
  }
}